.slideContainer {
   position: relative;
   min-width: 100%;
   height: 100%;
}

.slideContainer img {
   position: absolute;
   top: 0;
   left: 0;
   object-fit: cover;
   z-index: -1;
}

.slideTitle {
   padding: 40px;
   display: flex;
   flex-direction: column;
   color: #212121;
   font-size: 44px;
   font-style: normal;
   font-weight: 600;
   line-height: 48px;
   letter-spacing: -2%;
}