* {
   font-family: 'Inter', sans-serif;
}

li {
   list-style: none;
}

input {
   border: none;
}

button {
   border: none;
   cursor: pointer;
   /* display: inline-block; */
}

.App {
   display: flex;
   flex-direction: column;
   min-height: 100vh;
}

.wrapper {
   flex: 1 0 auto;
}

.rowBlock {
   display: flex;
   justify-content: space-between;
}

/* Колонки */
.columnContainer {
   display: flex;
}

.columnLarge {
   width: calc(76% - 10px);
}

.columnSmal {
   margin-left: 20px;
   width: calc(24% - 10px);
}

/* Граница */
.borderMain {
   padding: 40px;
   border-radius: 40px;
   border: 1px solid #DFDFDF;
}

/* Карточка */
.cardContainer {
   display: flex;
   justify-content: space-between;

}

/* Состояние кнопок */
.btnY {
   background: #EEEE20;
}

.btnY:hover {
   background: #FCFC3B;
}

.btnY:active {
   background: #ECE513;
}

/*  */
.btnW {
   border: 1px solid transparent;
}

.btnW:hover {
   color: #DCDCDC;
   border: 1px solid #EEEE20;
}

.btnW:hover img {
   opacity: .2;
}

.btnW:active {
   color: #303030;
   border: 1px solid #EEEE20;
}

.btnW:active img {
   opacity: 1;
}

/*  */
.btnCircle {
   background: #ffff;
   box-shadow: 0px 13px 33px 1px rgba(71, 71, 71, 0.24);
}

.btnCircle:hover {
   background-color: #F6F6F6;
}

.btnCircle:active {
   background-color: #EDEDED;
}
.btnCircle:active img {
   opacity: 1;
   background: #EDEDED;
}

/*  */
.btnRad {
   background: radial-gradient(446.15% 170.03% at 105.79% 213.75%, #F5982A 0%, #EEEE20 100%);
}

.btnRad:hover {
   background: radial-gradient(446.15% 170.03% at 105.79% 213.75%, #E1A522 0%, #FCFC3B 100%);
}

.btnRad:active {
   background: radial-gradient(446.15% 170.03% at 105.79% 213.75%, #D37300 0%, #ECE513 100%);
}

